<template>    
    <div class="tfBar">
        <header class="aa">            
            <div style="width:100%">
                <input type="text" class="search" placeholder="查询英文或中文">
                <img src="images/search.png" class="search-p">
            </div>
            <div>
                <img class="top-bell" src="images/bell.png">
            </div>
        </header>                
        <router-view></router-view>                     
            <nav class="mui-bar mui-bar-tab">
                <a class="mui-tab-item mui-active" @click="jump(1)">
                    <span class="mui-icon mui-icon-home"></span>
                    <span class="mui-tab-label">首页</span>
                </a>
                <a class="mui-tab-item"  @click="jump1(2)">
                    <span class="mui-icon"><img v-if="num==2" class="bar-3" src="images/cap-blue.png"><img v-else class="bar-3" src="images/cap.png"></span>
                    <span class="mui-tab-label bar-3_1">课程</span>
                </a>                
                <a class="mui-tab-item" @click="jump2(3)">
                    <span class="mui-icon"><img v-if="num==3" class="bar-3" src="images/ios-compass-blue.png"><img v-else class="bar-3" src="images/ios-compass.png"></span>
                    <span class="mui-tab-label bar-3_1">发现</span>
                </a>
                <a class="mui-tab-item" @click="jump3(4)">
                    <span class="mui-icon mui-icon-contact"></span>
                    <span class="mui-tab-label">我的</span>
                </a>
            </nav>
    </div>
</template>
<script>
export default {
    data(){
        return{
            num:0
        }
    },
    methods:{
        jump(e){            
            this.$router.push("/index")
            this.num = e 
        },
        jump1(e){            
            this.$router.push("/project")
            this.num = e            
        },
        jump2(e){            
            this.$router.push("/find")
            this.num = e
        },
        jump3(e){            
            this.$router.push("/userCenter")
            this.num = e
        }
    }
}
</script>
<style scope>
    *{
        touch-action:pan-y;
    }
    .tfBar{
        background: #fff;
        position: relative;
    }
    .search{
        width:97% !important;
        height:1.8rem;
        margin:0 !important;
        font-size: 0.8rem;
        background: #eee !important;
        border-radius:0 !important;
        border: 0 !important;
        padding-left:2rem !important;
        position: relative;
    }
    .aa{
        width: 100%;
        background: #fff;
        display: flex;
        padding:0.5rem 0.9rem;
        align-items:center;
        position: fixed;
        top:0px;
        z-index: 5;
        border: 0px;
        
    }
    .top-bell{
        width:1.1rem;
        height: 1.1rem;                  
    }
    .search-p{
        position: absolute;
        top:0.9rem;
        left:1rem;
    }
</style>
