<template>
    <div class="shopcar">
        <div class="head">
            <a href="#" @click="goto"><img src="images/X.png"></a>
            <p>购物车</p>
        </div>
        <div class="shopcar-K">
            <a href="#" v-show="kaiguan" @click="bianji">
                <img src="images/pencil.png"/>编辑
            </a>
            <p @click="wanC" v-show="kz">完成</p>
        </div>
        <div class="shopcar-list" v-for="(item,i) of details" :key="i">
            <div class="cartlist-l">
                <input type="checkbox" v-model="item.isSelected" @change="oneSelected">
                <img class="listpic" :src="`http://127.0.0.1:5000/img/${item.cimg}`" />
                <div class="listcontent">
                    <a href="#"><div v-text="item.title"></div></a>
                    <div v-text="details[i].produce"></div>
                    <div>贝壳抵用￥<span v-text="details[i].dikou"></span></div>
                </div>
            </div>
            <div class="cartlist-r">
                <div v-text="details[i].price"></div>
                <div class="addbtn" v-show="kaiguan">
                    <button @click="reduce(item.id)">-</button>
                    <div>{{item.count}}</div>
                    <button @click="add(item.id)">+</button>
                </div>
                <div class="delBnt" v-show="kz">
                    <input type="submit" value="删除">
                </div>
            </div>
        </div>
        
        <div class="footer">
            <div class="fot-l"> 
                <input type="checkbox" v-model="isAllSelected" @change="allSelected">
                <div>全选</div>
            </div>
            <div class="fot-r">
                <div class="fot-r1">￥{{allTotal()}}</div>
                <div class="fot-r2">
                    <button>结算（<span>{{allCount()}}</span>）</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            kaiguan:true,
            kz:false,
            isAllSelected:false,
            total:0,
            pid:0,
            details:[],
            totalCount:0           
        }
    },
    methods:{
        goto(){
            this.$router.push("/find")
        },
        bianji(){
            if(this.kaiguan!=false){
                this.kaiguan=false;
                this.kz=true;
            }
        },
        wanC(){
            if(this.kz!=false){
                this.kz=false;
                this.kaiguan=true;
            }
        },
        loadMore(){
            var url = "http://127.0.0.1:5000/getCart?uid="+this.pid;
            this.axios.get(url).then(result=>{
               this.$store.state.products  = result.data.data;
               this.details = this.$store.state.products
                for (var i=0;i<this.details.length;i++){
                    this.details[i].isSelected=false
                }
            })
        },
        allSelected(){
            for(var i=0;i<this.details.length;i++){
                this.details[i].isSelected = this.isAllSelected
            }
        },
        allTotal(){
            var allTotal = 0;
            for(var i=0;i<this.details.length;i++){
                if(this.details[i].isSelected){
                    var p = this.details[i]
                    allTotal += (p.count*p.price)
                }                
            }
            return allTotal.toFixed(2)
        },
        oneSelected(){
            var result =true;
            for(var i=0;i<this.details.length;i++){
                //console.log(this.details[i].isSelected)
                if(!this.details[i].isSelected){
                    result = false;
                    break;
                }
            }
            this.isAllSelected = result
            this.allCount()
        },
        allCount(){
            console.log(this.details)
            var num = 0;       
            for(var i=0;i<this.details.length;i++){
                if(this.details[i].isSelected){
                num +=this.details[i].count
                }                            
            }             
            return num 
        },
        reduce(id){
            //console.log(id)
            var smcount=0;
            var allTotal = 0;
            for(var i=0;i<this.details.length;i++){
                if(this.details[i].id == id){
                    if(this.details[i].count==1){
                        return
                    }else{
                     this.details[i].count--
                     smcount+=this.details[i].count
                    }
                   var p = this.details[i]
                   allTotal = p.count*p.price
                }                
            }
            //console.log(allTotal)
            //console.log(smcount)           
        },
        add(id){
            console.log(id);
            //var allTotal = 0;
            for(var i=0;i<this.details.length;i++){
               if(this.details[i].id == id){
                this.details[i].count++
                this.$store.state.products=this.details
               }                
            }
           //console.log(allTotal)                                  
        },
        
    },
    created(){
        this.pid = window.sessionStorage.getItem("id")
         this.loadMore();
         //console.log(this.details)
    },    
    updated(){
                    
    },
    computed:{
        
    }
}
</script>

<style>
    .shopcar{
        position: relative;
    }
    .shopcar .head{
        width:100%;
        background: #fff;
        display: flex; 
        align-items: center;
        padding:1rem 1rem;
        border-bottom:1px solid rgb(200,200,200);
    }
    .shopcar .head>a>img{
        line-height: 1rem;
        vertical-align:middle;
    }
    .shopcar .head>p{
        margin: 0 0 0 2rem;
        font-size:1.2rem;        
        color:rgb(80,80,80);
    }
    .shopcar-K{
        text-align: right;
        height: 2.5rem;       
        line-height: 2.5rem;
        padding:0 0.5rem 0 0;
    }
    .shopcar-K>a>img{
        width:0.8rem;
    }
    .shopcar-K>a{
        font-size:0.8rem;        
        color:#666;
    }
    .shopcar-K>p{
        font-size:0.9rem;        
        color:#666;
        letter-spacing: 1px;
    }
    .shopcar-list{
        background: #fff;
        display: flex;
        height: 6.5rem;
        padding:0.5rem
    }    
    .listpic{
        width:5rem;
        height:5rem;
    }
    .listcontent{
        height: 100%;
        display: flex;
        flex-flow: column;        
    }
    .listcontent>a>div{
        font-size:0.7rem;
        color: #555;
    }
    .listcontent :nth-child(1){
        height: 33.3333%;
    }
    .listcontent :nth-child(2){
        font-size:0.8rem;
        color:#ddd;
        height: 33.3333%;
    }
    .listcontent :nth-child(3){
        font-size: 0.6rem;
        background: orange;
        color: #fff;
        width:60%;
        border-radius: 3px;
        text-align: center;
        height: 25%;     
    }
    .listcontent>p{
        font-size:0.5rem;
    }
    .cartlist-l{
        width:75%;
        display: flex;        
        align-items: center;
        justify-content: space-around;
    }
    .addbtn{
        display: flex;
        align-items: center !important;
        justify-content: space-around;
    }
    .addbtn>div{
        text-align: center;
        line-height: 2rem;
        width:2rem;
        height: 2rem;          
    }
    .shopcar .addbtn>button{
        text-align: center !important;
        line-height: 0.5rem !important;
        padding:0;
        width:1.5rem;
        height: 1.4rem;
       font-size:2rem;
        color: #ddd;
    }
    .cartlist-r{
        width:25%;
        height: 100%;
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        text-align: center;        
        padding:0.3rem 0.5rem;
    }
    .delBnt{
        text-align: right;
    }
    .delBnt>input{
        width:4rem;
        height: 1.8rem !important;
        text-align: center !important;
        font-size:0.8rem !important;
        letter-spacing: 0.2rem !important;
        font-weight: normal;
        background: rgb(245, 57, 57) !important;
        border:0 !important;
        color:#fff !important;
    }
    .cartlist-r :first-child{
        text-align: right;
        font-size:1.2rem;
        font-weight: bold;
        color:lightseagreen;
    }
    .shopcar .footer{
        width: 100%;
        height: 3rem;
        background: #fff;
        position: fixed;
        bottom:3.125rem;    
    }
    .footer{
        display: flex;
        justify-content: space-between;
        padding: 0.5rem 0.5rem 0.5rem 0.8rem;
    }
    .fot-l{
        display: flex;
        align-items: center;
    }
    .fot-l>div{
        margin-left:0.5rem;
        color:#666;
    }
    .fot-r{
        display: flex;
        align-items: center;
    }
    .fot-r1{
        font-size:1.3rem;
        color:lightseagreen;
        font-weight: bold;
    }
    .fot-r2>button{
        background: lightseagreen;
        color:#fff;
        font-size:1.1rem;
        text-align: center;
        border:0;
        padding:0.5rem 0 0.3rem 0.7rem;
        margin-left:0.5rem;
    }
</style>

