<script>
export default {
    data(){
        return{            
            show:true,
            uimg:"",
            uname:"",
            uid:0
        }
    },
    methods:{
        tologin(){
            this.$router.push("/login");
        },
        back(){
            sessionStorage.removeItem("id")
            sessionStorage.removeItem("uid")
            sessionStorage.removeItem("uimg")
            sessionStorage.removeItem("uname")
            if(this.uname==""){
                this.show = false
            }
            this.$router.push("/login")          
        }
    },
    created(){               
        this.uid=window.sessionStorage.getItem("uid")
        this.uname=window.sessionStorage.getItem("uname")
        this.uimg=window.sessionStorage.getItem("uimg")
        console.log(this.uimg)
        console.log(this.uname)
        console.log(this.uid)
        if(this.uname){
            this.show = false
        }else{
           this.show = true
        }        
    },
    mounted(){
        
    }
}
</script>
