<script>
import {Toast} from "mint-ui";
export default {
    data(){
        return{
            show:true,
            dis:true,
            count:1,
            pid:0,
            id:0,
            details:[]
        }
    },
    created() {
        this.id = window.sessionStorage.getItem("id")
        console.log(this.id)
    },
    methods:{
        jump(){
            if(this.id == null){
                Toast("请登录")
                this.$router.push("/login")
            }else{
            this.$router.push({
                path:"/shopcar",
                query:{count:this.count,pid:this.pid},
                })}
        },
        goto(){
            this.$router.push("/shopping")
        },
        addcart(){
            this.show = false;             
        },
        hide(){
            this.show = true;
        },
        reduce(){            
            if(this.count==1){
                this.sid = true;
                return             
            }
            this.count-=1
        },
        add(){
            this.count+=1
            if(this.count>1){
                this.dis = false
            }
        },
        loadMore(){
            var url = "http://127.0.0.1:5000/getDetail?pid="+this.pid;
            this.axios.get(url).then(result=>{
                this.details = result.data[0]
                //console.log(this.details)
            })
        }
    },   
    mounted(){
        this.pid = this.$route.query.pid
        //console.log(this.pid)
        this.loadMore();
    },
    
}
</script>

