import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartCount:sessionStorage.getItem("carCount")||0 ,
    products:[],
    totalCount:0,
  },
  mutations:{
    totalCount(state){
      state.cartCount++;
      sessionStorage.setItem("carCount",state.cartCount);
    },
    totaljian(state){
      if(state.cartCount==0){
        return
      }
      state.cartCount--;
      sessionStorage.setItem("carCount",state.cartCount);
    }
  },
  getters:{
    getCount:function(state){
      return state.cartCount;
    }
  },
  actions: {}
});
