import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import index from "./components/index.vue";
import find from "./components/find.vue";
import project from "./components/project.vue";
import titleFoot from "./components/titleFoot.vue";
import userCenter from "./components/userCenter.vue";
import shopping from "./components/shopping.vue";
import carFoot from "./components/carFoot.vue";
import carCenter from "./components/carCenter.vue";
import shopcar from "./components/shopcar.vue";
import jingxuan from "./components/jingxuan.vue";
import details from "./components/details.vue";
import login from "./components/login.vue";
import exam from "./components/exam.vue";

Vue.use(Router);

export default new Router({
  // mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/home",
      name: "home",
      component: Home
    },
    {
      path: "/exam",
      name: "exam",
      component: exam
    }, 
    {
      path: "/jingxuan",
      name: "jingxuan",
      component: jingxuan
    },   
    {
      path: "/details",
      name: "details",
      component: details
    },
    {
      path: "/login",
      name: "login",
      component: login
    },    
    {
      path: "/carFoot",
      name: "carFoot",
      component: carFoot,
      redirect: "shopping",
      children: [
        {
          path: "/carCenter",
          name: "carCenter",
          component: carCenter
        },
        {
          path: "/shopping",
          name: "shopping",
          component: shopping,          
        },
        {
          path: "/shopcar",
          name: "shopcar",
          component: shopcar
        },
      ]
    },    
    {
      path: "/",
      name: "titleFoot",
      component: titleFoot,
      redirect: "/index",
      children: [
        {
          path: "/index",
          name: "index",
          component: index
        },
        {
          path: "/project",
          name: "project",
          component: project
        },
        {
          path: "/find",
          name: "find",
          component: find         
        },
        {
          path: "/userCenter",
          name: "userCenter",
          component: userCenter
        },        
      ]
    },
    
    
    
    {
      path: "/about",
      name: "about",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "about" */ "./views/About.vue")
    }
  ]
});
